@tailwind base;
@tailwind components;
@tailwind utilities;

@layer base {
  html {
    scroll-behavior: smooth;
  }
  
  body {
    font-family: 'Inter', sans-serif;
    background-color: #0d1b2a;
    color: #f3f4f6;
  }
  
  h1, h2, h3, h4, h5, h6 {
    font-family: 'Montserrat', sans-serif;
  }
}

@layer components {
  .btn-primary {
    @apply bg-servitec-orange hover:bg-orange-600 text-white font-semibold py-3 px-6 rounded-xl transition-all duration-300 hover:scale-105 shadow-lg hover:shadow-xl;
  }
  
  .btn-secondary {
    @apply bg-transparent border-2 border-servitec-orange text-servitec-orange hover:bg-servitec-orange hover:text-white font-semibold py-3 px-6 rounded-xl transition-all duration-300 hover:scale-105;
  }
  
  .card {
    @apply bg-white rounded-2xl shadow-lg hover:shadow-xl transition-all duration-300 hover:scale-105 p-6;
  }
  
  .gradient-text {
    @apply bg-gradient-to-r from-servitec-blue-light to-servitec-blue-lighter bg-clip-text text-transparent;
  }
  
  .section-padding {
    @apply py-16 px-4 sm:px-6 lg:px-8;
  }
  
  .container-custom {
    @apply max-w-7xl mx-auto;
  }
}
